import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { login, setRedirect, startSetRrppActive } from "../../actions/auth";
import { useForm } from "../../hooks/useForm";
// import eventIcon from '../../images/logoo2.png';
import "./login.css";
import {isMobile} from 'react-device-detect';
import { Link, useSearchParams } from "react-router-dom";
import { fetchSinToken } from "../../helpers/fetch";
import Swal from "sweetalert2";
import { useEffect } from "react";
import { GoogleLogin } from '@react-oauth/google';
import { useState } from "react";

export const LoginScreen = () => {
  const dispatch = useDispatch();
  const { uid } = useSelector((state) => state.auth);
  const [formLoginValues, handleLoginInputChange] = useForm({
    lEmail: "",
    lPassword: "",
  });
  const { lEmail, lPassword } = formLoginValues;
  const [searchParams] = useSearchParams();
  const [goTo, setGoTo] = useState("/");
  const [isEvento, setIsEvento] = useState(false);
  const [eventoGo , setEventoGo  ] = useState(null);
  useEffect(() => {
    setEventoGo(searchParams.get('evento'));
    // const eventoGo = searchParams.get('evento');
    if(eventoGo!==undefined && eventoGo!==null){
      setGoTo(`/evento/${eventoGo}`)
      setIsEvento(true);
    }
  }, [searchParams,eventoGo])

  const handleLogin = (e) => {
    e.preventDefault();
    const startLogin = async(emaill, password) => {
      dispatch(setRedirect(goTo))
      let email = emaill.toLowerCase();
      const resp = await fetchSinToken("auth", { email, password }, "POST");
      const body = await resp.json();
      if (body.ok) {
        localStorage.setItem("token", body.token);
        localStorage.setItem("token-init-date", new Date().getTime());
        dispatch(login({ uid: body.uid, nombre: body.nombre, rol: body.rol, maxFree: body.maxFree }));
        dispatch(startSetRrppActive( body.uid ));
        // navigate(-1);
        // navigate(goTo);
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    }
    startLogin(lEmail, lPassword);
  };

  const onSuccess = (res) => {
    const startGoogle = async(res) => {
      const resp = await fetchSinToken(`auth/newgoogle`,{credential: res.credential}, "POST");
      const bodi = await resp.json();
      if(bodi.isVerified){
        const startLoginGoogle = async(email,nombre,apellido) => {
          dispatch(setRedirect(goTo))
          const resp = await fetchSinToken("auth/google", { email,nombre,apellido }, "POST");
          const body = await resp.json();
          if (body.ok) {
            localStorage.setItem("token", body.token);
            localStorage.setItem("token-init-date", new Date().getTime());
            dispatch(login({ uid: body.uid, nombre: body.nombre, rol: body.rol }));
            dispatch(startSetRrppActive( body.uid ));
            // navigate(goTo);
            // navigate('/');
          } else {
            Swal.fire("Error", body.msg, "error");
          }
        }
        startLoginGoogle(bodi.email,bodi.nombre,bodi.apellido);
      }
    }
    startGoogle(res);
  };
  const onError = (err) => {
    Swal.fire("Error", "Error al iniciar con Google", "error");
  };
  
  return (
    <>
    { isMobile ?
    <div className="login-container" data-aos="fade-down" data-aos-duration="1000">
{/* <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#000" }}><img src={eventIcon} alt="" /></h2> */}
    <div className="row">
      <div className="col-md-11 col-11 login-form-1 mt-5">
        <h3>INGRESO</h3>
        <form onSubmit={handleLogin}>
          <div className="form-group">
            <input
              type="text"
              className="form-control mb-2"
              placeholder="Correo"
              name="lEmail"
              value={lEmail}
              onChange={handleLoginInputChange}
            />
          </div>
          <div className="form-group">
            <input
              type="password"
              className="form-control mb-3"
              placeholder="Contrasea"
              name="lPassword"
              value={lPassword}
              onChange={handleLoginInputChange}
            />
          </div>
          <div className="form-group d-flex justify-content-center">
            <input type="submit" className="btn btn-success" value="Ingresar" />
          </div>
          <div className="d-flex justify-content-center align-items-center mt-3">
              <GoogleLogin
              onSuccess={onSuccess}
              onError={onError}
              />
            </div>
        </form>
      </div>

    </div>
  </div> 
  : 
  <div className="login-container" data-aos="fade-up" data-aos-duration="1000">
      {/* <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#000" }}><img src={eventIcon} alt="" /></h2> */}
      <div className="row mt-4">
        <div className="col-md-6 col-6 me-5 login-form-3">
          <h3>INGRESO</h3>
          <form onSubmit={handleLogin}>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
            <i style={{color:"white"}} className="m-3 fa-solid fa-envelope fa-lg"></i>
              <input
                type="text"
                className="form-control"
                placeholder="Correo"
                name="lEmail"
                value={lEmail}
                onChange={handleLoginInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
            <i style={{color:"white"}} className="m-3 fa-solid fa-lock fa-lg"></i>
              <input
                type="password"
                className="form-control"
                placeholder="Contrasea"
                name="lPassword"
                value={lPassword}
                onChange={handleLoginInputChange}
              />
            </div>
            <div className="form-group d-flex justify-content-center">
              <input type="submit" className="btn btn-outline-success" value="Ingresar" />
            </div>
            <div className="d-flex justify-content-center align-items-center mt-3">
              <GoogleLogin
              onSuccess={onSuccess}
              onError={onError}
              />
            </div>
          </form>
          <div className="form-group d-flex justify-content-center">
            <h5 className="text-light">No ests registrado? Registrate <Link className='text-decoration-none text-primary' to={isEvento?`/register?evento=${eventoGo}`:`/register`}>AQU</Link></h5>
          </div>
        </div>

      </div>
    </div>
  }
  </>
  );
};
