import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { fetchConToken } from '../../helpers/fetch';
import moment from 'moment';
// import Swal from 'sweetalert2';
import { isMobile, useDeviceSelectors } from 'react-device-detect';
import { uiOpenModal, uiOpenModall } from '../../actions/ui';
import 'moment/locale/es';
import { Loading } from '../../router/Loading';
import { CompraModal } from './CompraModal';
import { CanjearModal } from './CanjearModal';
import Swal from 'sweetalert2';
import { Compra2Modal } from './Compra2Modal';

export const EventoDetalle = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { uid } = useSelector((state) => state.auth);
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const [evento, setEvento] = useState(null);
  const [isAuthed, setIsAuthed] = useState(false);
  const [precioGeneral, setPrecioGeneral] = useState(0);
  const [tipoentradas, setTipoentradas] = useState([]);
  const [precioVip, setPrecioVip] = useState(0);
  const [selectors, data] = useDeviceSelectors(window.navigator.userAgent);
  // let windowReference;
  // if(){
  //   windowReference = window.open();
  // }
  //FETCHING
  useEffect(() => {
    const getEvento = async () => {
      const resp = await fetchConToken(`eventos/detalle/${idEvento}`);
      const body = await resp.json();
      if (!body.ok) {
        Swal.fire('Error!', "No existe ese evento", 'error')
        navigate('/');
      } else {
        setEvento(body.evento);
        if (body.evento.tipoentradas.length > 0) {
          setTipoentradas(body.evento.tipoentradas);
          for (let i = 0; i < body.evento.tipoentradas.length; i++) {
            if (body.evento.tipoentradas[i].descripcion.toUpperCase() === "GENERAL") {
              if (body.evento.tipoentradas[i].visible) {
                setPrecioGeneral(body.evento.tipoentradas[i].precio);
              }
            }
            if (body.evento.tipoentradas[i].descripcion.toUpperCase() === "VIP") {
              if (body.evento.tipoentradas[i].visible) {
                setPrecioVip(body.evento.tipoentradas[i].precio);
              }
            }
          }
        }
      }
    }
    getEvento();
    if (!!uid) {
      setIsAuthed(true);
    }

  }, [idEvento, uid, navigate]);
  const handleGoBuy = () => {
    const getEvento = async () => {
      const resp = await fetchConToken(`eventos/detalle/${idEvento}`);
      const body = await resp.json();
      if (!body.ok) {
        Swal.fire('Error!', "No existe ese evento", 'error')
        navigate('/');
      } else {
        setEvento(body.evento);
        if (body.evento.tipoentradas.length > 0) {
          setTipoentradas(body.evento.tipoentradas);
        }
      }
    }
    getEvento();
    dispatch(uiOpenModal());
  };
  const handleCanjear = () => {
    dispatch(uiOpenModall());
  };
  // const handleEvento = (evento) => {

  // };
  return (
    <>
      {isMobile
        ?
        <div>
          {evento ?
            <div className="container-fluid d-flex flex-column justify-content-center align-items-center" data-aos="fade-up" data-aos-duration="1000">
              <div className='d-flex flex-column justify-content-center align-items-center mb-3'>
                <Link className='text-decoration-none mb-3' to={`/evento/${evento.id}`} style={{ borderRadius: "2%", border: "1px solid black" }}>
                  <div className="justify-content-end bg-black" style={{ width: "250px", height: "300px", borderRadius: "inherit", border: "1px solid black" }}>
                    <img src={evento.foto ? evento.foto : `https://res.cloudinary.com/dszsrf58b/image/upload/v1661815575/party-night-banner-design-template-9c9d0ee04d93c1e567f473f6a683c61c_screen_m68fcr.jpg`} style={{ objectFit: "fill", width: "100%", height: "100%", borderRadius: "inherit", border: "1px solid black" }} alt="evento" />
                  </div>
                </Link>

                {precioGeneral > 0 && <h6 className="text-light text-center mb-2">Entradas GENERALES desde $<b className='text-info'>{precioGeneral}</b></h6>}
                {precioVip > 0 && <h6 className="text-light text-center mb-2">Entradas VIP desde $<b className='text-info'>{precioVip}</b></h6>}
                {isAuthed
                  ?
                  // evento.ticketGen>=evento.ticketMax
                  // ?
                  // <div className='d-flex flex-column justify-content-center align-items-center'>
                  //   <button onClick={handleCanjear} className='btn btn-sm btn-outline-primary mb-3'>CANJEAR CODIGO</button>
                  //   <h5 className="text-center text-danger mb-2"><b>ENTRADAS AGOTADAS</b></h5>
                  // </div>
                  // :
                  // data.os.name === "iOS" ?
                  //   <h6 className="text-light text-center mb-2"><b className='text-info'>Compra NO apta para iOS</b></h6>
                  //   :
                  <div className='d-flex flex-column justify-content-center align-items-center'>
                    <button onClick={handleCanjear} className='btn btn-sm btn-outline-primary mb-2'>CANJEAR CODIGO</button>
                    <button onClick={handleGoBuy} className='btn btn-lg btn-success'>COMPRAR</button>
                  </div>
                  :
                  <h5 className="text-center text-light mb-2"><Link className='btn btn-outline-primary text-decoration-none ' to={`/login?evento=${idEvento}`}><b>Ingrese para comprar</b></Link></h5>
                }
              </div>
              <div className="d-flex flex-column justify-content-center align-items-center text-center">
                <h1 className="text-light text-center mb-1">{evento.nombre.toUpperCase()}</h1>

                <hr style={{ color: "white" }} />

                <div className="m-1 d-flex flex-column justify-content-center align-items-center">
                  <h3 className="text-light mb-1"><i className="fa-solid fa-circle-info"></i> INFORMACIN</h3>
                  {/* <span className="text-light"> Informacion del evento</span> */}
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1 d-flex flex-column justify-content-center align-items-center">
                  <h3 className="text-light mb-3"><i className="fa-solid fa-house"></i> Lugar</h3>
                  <span className="text-light"> {evento.ubicacion.toUpperCase()}</span>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1 d-flex flex-column justify-content-center align-items-center">
                  <h3 className='text-light mb-3 '><i className="fa-solid fa-calendar"></i> Fecha</h3>
                  <p className="text-info mb-3"> {moment(evento.fecha).format('LL')}</p>
                  <p className="text-light">Horario de entrada al evento: <b>{evento.horario ? evento.horario : "00:00"} hs</b></p>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className='text-light'><i className="fa-solid fa-compact-disc"></i> Pistas</h3>
                  <p className="text-light"> {evento.pista ? evento.pista : "CACHENGUE"}</p>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1 d-flex flex-column justify-content-center align-items-center">
                  <h3 className="text-light"><i className="fa-solid fa-location-dot"></i> Ubicacion</h3>
                  <p className="text-light" style={{ fontSize: "15px" }}>{evento.direccion ? evento.direccion : "Blvd. Juan Domingo Pern 2907, S3560 Reconquista, Santa Fe"}</p>
                  <a href={evento.urlgoogle ? evento.urlgoogle : 'https://www.google.com.ar/maps/place/PORTOALEGRE+fiestas/@-29.1532704,-59.674743,17.81z/data=!4m12!1m6!3m5!1s0x944eb06b8c025727:0x558446f4036a6842!2sPORTOALEGRE+fiestas!8m2!3d-29.1532113!4d-59.6741865!3m4!1s0x944eb06b8c025727:0x558446f4036a6842!8m2!3d-29.1532113!4d-59.6741865'} target="_blank" className="btn-hover rounded-pill bg-dark p-2 text-info text-decoration-none" style={{ fontSize: "15px" }} rel="noreferrer"><i className="fa-solid fa-location-crosshairs"></i> Abrir en el mapa</a>
                </div>

                <hr style={{ color: "white" }} />

                {(evento.iglink || evento.facelink) && <div className="m-2 d-flex flex-column justify-content-center align-items-center">
                  <h3 className="text-light"><i className="fa-solid fa-hashtag"></i> Redes</h3>
                  <div className='d-flex justify-content-center align-items-center mb-3'>
                    {evento.iglink && <a href={evento.iglink} className="text-light text-decoration-none" style={{ fontSize: "15px" }}><i className="fab fa-instagram fa-xl"></i> Instagram</a>}
                  </div>
                  <div className='d-flex justify-content-center align-items-center mb-3'>
                    {evento.facelink && <a href={evento.facelink} className="text-light text-decoration-none" style={{ fontSize: "15px" }}><i className="fab fa-facebook fa-xl"></i> Facebook</a>}
                  </div>
                </div>}

                <hr style={{ color: "white" }} />

              </div>
            </div>
            :
            <Loading checking={!!evento} />
          }
        </div>
        :
        <div>
          {evento ?
            <div className="d-flex flex-row justify-content-between align-items-start p-5 ms-5" data-aos="fade-up" data-aos-duration="1000">
              <div className='d-flex flex-column justify-content-center align-items-center'>
                <Link className='text-decoration-none mb-3' to={`/evento/${evento.id}`} style={{ borderRadius: "2%", border: "1px solid black" }}>
                  <div className="justify-content-end bg-black" style={{ width: "250px", height: "300px", borderRadius: "inherit", border: "1px solid black" }}>
                    <img src={evento.foto ? evento.foto : `https://res.cloudinary.com/dszsrf58b/image/upload/v1661815575/party-night-banner-design-template-9c9d0ee04d93c1e567f473f6a683c61c_screen_m68fcr.jpg`} style={{ objectFit: "fill", width: "100%", height: "100%", borderRadius: "inherit", border: "1px solid black" }} alt="evento" />
                  </div>
                </Link>

                {precioGeneral > 0 && <h6 className="text-light text-center mb-2">Entradas GENERALES desde <p>$<b className='text-info'>{precioGeneral}</b></p></h6>}
                {precioVip > 0 && <h6 className="text-light text-center mb-2">Entradas VIP desde <p>$<b className='text-info'>{precioVip}</b></p></h6>}
                {isAuthed
                  ?
                  // evento.ticketGen>=evento.ticketMax
                  // ?
                  // <div className='d-flex flex-column justify-content-center align-items-center'>
                  //   <button onClick={handleCanjear} className='btn btn-sm btn-outline-primary mb-3'>CANJEAR CODIGO</button>
                  //   <h5 className="text-center text-danger mb-2"><b>ENTRADAS AGOTADAS</b></h5>
                  // </div>
                  // :
                  <div className='d-flex flex-column justify-content-center align-items-center'>
                    <button onClick={handleCanjear} className='btn btn-sm btn-outline-primary mb-2'>CANJEAR CODIGO</button>
                    <button onClick={handleGoBuy} className='btn btn-lg btn-success'>COMPRAR</button>
                  </div>
                  :
                  <h5 className="text-center text-light mb-2"><Link className='btn btn-outline-primary text-decoration-none ' to={`/login?evento=${idEvento}`}><b>Ingrese para comprar</b></Link></h5>
                }
              </div>
              <div className="d-flex flex-column justify-content-start align-items-start w-100 pm-5 ps-5 m-5">
                <h1 className="text-light mb-1">{evento.nombre.toUpperCase()}</h1>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className="text-light mb-1"><i className="fa-solid fa-circle-info"></i> INFORMACIN</h3>
                  {/* <span className="text-light"> Informacion del evento</span> */}
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className="text-light mb-3"><i className="fa-solid fa-house"></i> Lugar</h3>
                  <span className="text-light"> {evento.ubicacion.toUpperCase()}</span>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className='text-light mb-3'><i className="fa-solid fa-calendar"></i> Fecha</h3>
                  <p className="text-info mb-3"> {moment(evento.fecha).format('LL')}</p>
                  <p className="text-light">Horario de entrada al evento: <b>{evento.horario ? evento.horario : "00:00"} hs</b></p>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className='text-light'><i className="fa-solid fa-compact-disc"></i> Pistas</h3>
                  <p className="text-light"> {evento.pista ? evento.pista : "CACHENGUE"}</p>
                </div>

                <hr style={{ color: "white" }} />

                <div className="m-1">
                  <h3 className="text-light"><i className="fa-solid fa-location-dot"></i> Ubicacion</h3>
                  <p className="text-light" style={{ fontSize: "15px" }}>{evento.direccion ? evento.direccion : "Blvd. Juan Domingo Pern 2907, S3560 Reconquista, Santa Fe"}</p>
                  <a href={evento.urlgoogle ? evento.urlgoogle : 'https://www.google.com.ar/maps/place/PORTOALEGRE+fiestas/@-29.1532704,-59.674743,17.81z/data=!4m12!1m6!3m5!1s0x944eb06b8c025727:0x558446f4036a6842!2sPORTOALEGRE+fiestas!8m2!3d-29.1532113!4d-59.6741865!3m4!1s0x944eb06b8c025727:0x558446f4036a6842!8m2!3d-29.1532113!4d-59.6741865'} target="_blank" className="btn-hover rounded-pill bg-dark p-2 text-info text-decoration-none" style={{ fontSize: "15px" }} rel="noreferrer"><i className="fa-solid fa-location-crosshairs"></i> Abrir en el mapa</a>
                </div>

                <hr style={{ color: "white" }} />

                {(evento.iglink || evento.facelink) && <div className="m-2">
                  <h3 className="text-light mb-3"><i className="fa-solid fa-hashtag"></i> Redes</h3>
                  <div className='d-flex justify-content-center align-items-center mb-3'>
                    {evento.iglink && <a href={evento.iglink} className="text-light text-decoration-none" style={{ fontSize: "15px" }}><i className="fab fa-instagram fa-xl"></i> Instagram</a>}
                  </div>
                  <div className='d-flex justify-content-center align-items-center mb-3'>
                    {evento.facelink && <a href={evento.facelink} className="text-light text-decoration-none" style={{ fontSize: "15px" }}><i className="fab fa-facebook fa-xl"></i> Facebook</a>}
                  </div>
                </div>}

                <hr style={{ color: "white" }} />

              </div>
            </div>
            :
            <Loading checking={!!evento} />
          }
        </div>
      }
      {/* <CompraModal tipoentradas={tipoentradas} evento={evento}/> */}
      {tipoentradas && tipoentradas.length > 0 && <Compra2Modal tipoentradas={tipoentradas} evento={evento} />}
      <CanjearModal evento={evento} />
    </>
  );
};
