import React from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import { uiCloseModal } from "../../actions/ui";
import "../modal.css";
import { useForm } from "../../hooks/useForm";
import { useState } from "react";
import Swal from "sweetalert2";
import { fetchConToken } from "../../helpers/fetch";
import { useDeviceSelectors } from "react-device-detect";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const CeluModal = ({evento}) => {
  const dispatch = useDispatch();
  const { modalOpen } = useSelector((state) => state.ui);
  const { uiid } = useSelector((state) => state.ui);
  const [formValues, handleInputChange] = useForm({
    telefono:""
  });
  const {telefono} = formValues;
  const closeModal = () => {
    dispatch(uiCloseModal());
  };
  const [selectors, data] = useDeviceSelectors(window.navigator.userAgent);
  const handleGoChange = () => {
    let windowReference;
    if(data.os.name==="iOS"){
      windowReference = window.open();
    }
    closeModal();
    const getPDF = async() => {
      const resp = await fetchConToken(`clientes/getpdf/${uiid.uiid}`);
      const body = await resp.json();
      if(body.ok){
        if(data.os.name==="iOS"){
          windowReference.location = `https://wa.me/549${telefono}?text=Hola! aqui te dejo el link con el QR para el ingreso al evento. Muchas gracias! ${body.url}`
        }else{
          window.open(`https://wa.me/549${telefono}?text=Hola! aqui te dejo el link con el QR para el ingreso al evento. Muchas gracias! ${body.url}`);
        }
        
        // window.open(`https://wa.me/549${telefono}?text=Hola! aqui te dejo el link con el QR para el ingreso al evento. Muchas gracias! ${body.url}`);
        // window.open(`${body.url}`)
      }else{
        Swal.fire("Error",body.msg,"error");
      }
    };
    getPDF();
    
  };
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","," "]);
  return (
    <tr className="d-none">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      >
      <div style={{color:"white"}} className="d-flex flex-column justify-content-center align-items-center text-center">
        <b style={{fontSize:"20px"}}>Ingrese el numero de telefono para enviar WSP</b>
        <b style={{fontSize:"16px"}}>Formato: Caracteristica+Numero. Ejemplo: 3482 123456</b>
      </div>
      <hr />
      <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <input
                type="number"
                className="form-control"
                placeholder="Numero de celular"
                name="telefono"
                autoComplete="off"
                value={telefono}
                onChange={handleInputChange}
                onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
              />
        </div>
        <div className="mt-4">
        <button onClick={()=>handleGoChange()} className="btn btn-outline-success btn-block">
          <i className="fab fa-whatsapp"></i>
          <span> Enviar</span>
        </button>
        </div>
      </Modal>
    </tr>
  );
};
