import React from "react";
import { useDispatch } from "react-redux";
import { setRedirect, startRegisterCliente } from '../../actions/auth';
import { useForm } from "../../hooks/useForm";
import "./login.css";
import {isMobile} from 'react-device-detect';
import Swal from "sweetalert2";
import { Link, useSearchParams } from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";

export const RegisterScreen = () => {
  const dispatch = useDispatch();
  const [formRegisterValues, handleRegisterInputChange] = useForm({
      rNombre: "",
      rApellido: "",
      rEmail: "",
      rPassword: "",
      rPasswordConf:"",
  });
  const { rEmail, rPassword, rNombre, rApellido, rPasswordConf } = formRegisterValues;
  const [searchParams] = useSearchParams();
  const [goTo, setGoTo] = useState("/");
  const [eventoGo , setEventoGo  ] = useState(null);
  useEffect(() => {
    setEventoGo(searchParams.get('evento'));
    if(eventoGo!==undefined && eventoGo!==null){
      setGoTo(`/evento/${eventoGo}`)
    }
  }, [searchParams,eventoGo])

  const handleRegister = (e) => {
    e.preventDefault();
    dispatch(setRedirect(goTo))
    if (rNombre === "" || rApellido === "" || rEmail === "" || rPassword === "" || rPasswordConf ==="") {
      return Swal.fire(
        "Error",
        "Debes de rellenar todos los campos",
        "error"
      );
    }
    if(rPassword!==rPasswordConf){
      return Swal.fire(
        "Error",
        "Las contraseas deben ser iguales",
        "error"
      );
    }
    dispatch(startRegisterCliente(rEmail, rPassword, rNombre, rApellido));
  };
  return (
    <>
    { isMobile ?
    <div className="login-container" data-aos="fade-down" data-aos-duration="1000">
    {/* <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#0062cc" }}><img src={eventIcon} alt="" /></h2> */}
    <div className="row vw-100">
      <div className="col-md-11 col-11 login-form-1 mt-5">
        <h3>REGISTRO</h3>
        <form onSubmit={handleRegister}>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-envelope fa-lg"></i>
              <input
                  type="text"
                  className="form-control"
                  placeholder="Correo"
                  name="rEmail"
                  autoComplete="off"
                  value={rEmail}
                  onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-lock fa-lg"></i>
              <input
                type="password"
                className="form-control"
                placeholder="Contrasea"
                name="rPassword"
                autoComplete="new-password"
                value={rPassword}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-arrows-rotate fa-lg"></i>
              <input
                type="password"
                className="form-control"
                placeholder="Repetir Contrasea"
                name="rPasswordConf"
                autoComplete="new-password"
                value={rPasswordConf}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-user fa-lg"></i>
              <input
                type="text"
                className="form-control"
                placeholder="Nombre"
                name="rNombre"
                value={rNombre}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-id-card fa-lg"></i>
              <input
                type="text"
                className="form-control"
                placeholder="Apellido"
                name="rApellido"
                value={rApellido}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex justify-content-center">
              <input type="submit" className="btn btn-outline-success" value="Registrarse" />
            </div>
          </form>
      </div>

    </div>
  </div> 
  : 
  <div className="login-container" data-aos="fade-up" data-aos-duration="1000">
      {/* <h2 className="d-flex align-items-center justify-content-center mt-4" style={{ color: "#0062cc" }}><img src={eventIcon} alt="" /></h2> */}
      <div className="row mt-4">
        <div className="col-md-6 col-6 me-5 login-form-3">
          <h3>REGISTRO</h3>
          <form onSubmit={handleRegister}>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-envelope fa-lg"></i>
              <input
                  type="text"
                  className="form-control"
                  placeholder="Correo"
                  name="rEmail"
                  autoComplete="off"
                  value={rEmail}
                  onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-lock fa-lg"></i>
              <input
                type="password"
                className="form-control"
                placeholder="Contrasea"
                name="rPassword"
                autoComplete="new-password"
                value={rPassword}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-arrows-rotate fa-lg"></i>
              <input
                type="password"
                className="form-control"
                placeholder="Repetir Contrasea"
                name="rPasswordConf"
                autoComplete="new-password"
                value={rPasswordConf}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-user fa-lg"></i>
              <input
                type="text"
                className="form-control"
                placeholder="Nombre"
                name="rNombre"
                value={rNombre}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex flex-row justify-content-center align-items-center mb-3">
              <i style={{color:"white"}} className="m-3 fa-solid fa-id-card fa-lg"></i>
              <input
                type="text"
                className="form-control"
                placeholder="Apellido"
                name="rApellido"
                value={rApellido}
                onChange={handleRegisterInputChange}
              />
            </div>
            <div className="form-group d-flex justify-content-center">
              <input type="submit" className="btn btn-outline-success" value="Registrarse" />
            </div>
          </form>
          <div className="form-group d-flex justify-content-center">
            <h5 className="text-light">Ya ests registrado? Ingres <Link className='text-decoration-none text-primary' to={`/login`}>AQU</Link></h5>
          </div>
        </div>
      </div>
    </div>
  }
  </>
  );
};
