import React, { useState } from 'react';
import QRCode from "qrcode.react";
import { useEffect } from 'react';
import { fetchConToken } from '../../helpers/fetch';
import { useDispatch } from 'react-redux';
import { stopWaitQr, waitQr } from '../../actions/ui';

export const QRGeneratorPagina = ({uid,numero,id,desc}) => {
  const tipo = "PAGA";
  const [hashed, setHashed] = useState("");
  const dispatch = useDispatch();  
  useEffect(() => {
    const hashIt = async()=>{
      dispatch(waitQr());
      const resp = await fetchConToken(`clientes/sethash`,{uid,numero,tipo},'POST');
      const body = await resp.json();
      setHashed("p"+desc+"-"+numero+"-"+body.hash);
      setTimeout(()=>{
        dispatch(stopWaitQr());
      },1500);
    }
    hashIt();
  }, [uid,numero,dispatch,desc]);
  return (
  <div>
      <QRCode id={id} className='d-none' value={`${hashed}`} size={384} level='L' />
  </div>
  );
};
