import React, { useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { fetchConToken } from "../../helpers/fetch";
import { PaginatedEntradasHistorialItems } from "./PaginatedEntradasHistorialItems";

export const HistorialCompraScreen = () => {
  const { uid } = useSelector((state) => state.auth);
  const navigate = useNavigate();
  const [entradas, setEntradas] = useState([]);
  const [loading, setLoading] = useState(false);
  //FETCHING
  useEffect(() => {
    const getEntradas = async () => {
      setLoading(true);
      const resp = await fetchConToken(`clientes/pagina/${uid}/historial`);
      const body = await resp.json();
      setEntradas(body.entradas);
      setLoading(false);
    }
    getEntradas();
  }, [uid]);
  const handleVolver = (e) => {
    navigate('/misentradas');
  };
  return (
    <div className="d-flex flex-column justify-content-center align-items-center text-center">
      <h1 className="text-light mb-2">Historial</h1>
      <hr style={{ color: "white" }} />

      {entradas.length > 0 ?
        <PaginatedEntradasHistorialItems itemsPerPage={10} items={entradas} loading={loading} />
        :
        <span className="h3 text-light">No hay registro</span>}
      <div className="form-group d-flex justify-content-center">
        <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
      </div>
    </div>
  );
};
