import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";

export const uiOpenModal=()=>({
    type:types.uiOpenModal
})
export const uiOpenModalConParams=(uid,dni)=>{
  return async (dispatch) => {
    dispatch(uiSetParams(uid,dni))
    dispatch(uiOpenModal())

    // try {
    //   const resp = await fetchConToken("clientes/cambiarcelu");
    //   const body = await resp.json();
      
    // } catch (error) {
    //   console.log(error);
    // }
  };
}
const uiSetParams=(uid,dni)=>({
  type:types.uiSetParams,
  payload:{uid,dni}
})

export const uiEnviarCelu=(uiid)=>({
  type:types.uiEnviarCelu,
  payload:{uiid}
})
export const startClearParams = () => ({
  type: types.startClearParams,
  payload: null,
});
export const uiCloseModal=()=>({
    type:types.uiCloseModal
})


