import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import { uiCloseModal } from "../../actions/ui";
import { pasarClientesPagina, startClearClienteAsoc } from "../../actions/clientes";
// import { getPrecios } from "../../actions/eventos";
import { useLocation, useNavigate } from "react-router-dom";
import Select from 'react-select';
import "../modalpay.css";
import Swal from "sweetalert2";
import { fetchConToken } from "../../helpers/fetch";
import MercadoPagoForm from "../mercadopago/MercadoPagoForm";
import moment from "moment";
import { QRGeneratorPagina } from "../ui/QRGeneratorPagina";
import { useCallback } from "react";


const customStyless = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    // marginRight: "-50%",
    // transform: "translate(-50%, -50%)",
  },
};
const colourStyles = {
  control: styles => ({ ...styles, width:'300px' }),
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const CompraModal = ({evento,tipoentradas}) => {
  const dispatch = useDispatch();
  const { uid } = useSelector((state) => state.auth);
  // const navigate = useNavigate();
  // console.log(tipoentradas)
  // const { waitQr } = useSelector((state) => state.ui);
  const { modalOpen, waitQr} = useSelector((state) => state.ui);
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const [cantidadTotalEvento, setCantidadTotalEvento] = useState(0);
  const [cantidadActualEvento, setCantidadActualEvento] = useState(0);
  const [isOk, setIsOk] = useState(false);
  const [cantidad, setCantidad] = useState(0);
  const [tipo, setTipo] = useState("");
  const [precios, setPrecios] = useState(tipoentradas);

  const qrCodeCanvas0 = document.querySelector('#canvas0');
  const dataUrl0 = qrCodeCanvas0&&qrCodeCanvas0.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas1 = document.querySelector('#canvas1');
  const dataUrl1 = qrCodeCanvas1&&qrCodeCanvas1.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas2 = document.querySelector('#canvas2');
  const dataUrl2 = qrCodeCanvas2&&qrCodeCanvas2.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas3 = document.querySelector('#canvas3');
  const dataUrl3 = qrCodeCanvas3&&qrCodeCanvas3.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas4 = document.querySelector('#canvas4');
  const dataUrl4 = qrCodeCanvas4&&qrCodeCanvas4.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas5 = document.querySelector('#canvas5');
  const dataUrl5 = qrCodeCanvas5&&qrCodeCanvas5.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas6 = document.querySelector('#canvas6');
  const dataUrl6 = qrCodeCanvas6&&qrCodeCanvas6.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas7 = document.querySelector('#canvas7');
  const dataUrl7 = qrCodeCanvas7&&qrCodeCanvas7.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas8 = document.querySelector('#canvas8');
  const dataUrl8 = qrCodeCanvas8&&qrCodeCanvas8.toDataURL('image/jpg', 0.3);
  const qrCodeCanvas9 = document.querySelector('#canvas9');
  const dataUrl9 = qrCodeCanvas9&&qrCodeCanvas9.toDataURL('image/jpg', 0.3);

  let tipos = [];
  if(precios){
    for (let i = 0; i < precios.length; i++) {
      if(precios[i].visible){
        const tipo = { value: precios[i], label: `${precios[i].descripcion.toUpperCase()} - $${precios[i].precio}`, disabled:precios[i].cantidad-precios[i].cantidadActual<=0?true:false};
        tipos.push(tipo);
      }
    }
  }else{
    const tipoa = { value: '', label: `NO HAY ENTRADAS EN EL EVENTO`};
    tipos.push(tipoa);
  }
  const closeModal = useCallback(() => {
    dispatch(uiCloseModal());
    dispatch(startClearClienteAsoc());
  },[dispatch]);

  // useEffect(() => {
  //   const getEvento = async() =>{
  //     const resp = await fetchConToken(`eventos/detalle/${idEvento}`);
  //     const body = await resp.json();
  //     if(!body.ok){
  //       Swal.fire('Error!',"No existe ese evento",'error');
  //       navigate('/');
  //     }else{
  //       if(body.evento.tipoentradas.length>0){
  //         setPrecios(body.evento.tipoentradas);
  //       }
  //     }
  //   }
  //   getEvento();
  // }, [dispatch,idEvento,closeModal,navigate]);
  const handleTipo = (e) => {
    setCantidadActualEvento(e.value.cantidadActual)
    setCantidadTotalEvento(e.value.cantidad)
    setTipo(e);
  };
  const handleReload = () => {
    const getEvento = async() =>{
      const resp = await fetchConToken(`eventos/detalle/${idEvento}`);
      const body = await resp.json();
      if(body.evento.tipoentradas.length>0){
        setPrecios(body.evento.tipoentradas);
      }
    }
    getEvento();
  };
  const incrementCount = () => {
    if(cantidad>=10){
      setCantidad(cantidad);
    }else{
      if(cantidadTotalEvento-cantidadActualEvento<=cantidad){
        setCantidad(cantidad);
      }else{
        setCantidad(cantidad+1);
      }
    }
  }
  const decrementCount = () => {
    if(cantidad<=1){
      setCantidad(cantidad);
    }else{
      setCantidad(cantidad-1);
    }
  }
  const handleGoPagar = () => {
    // Swal.fire({
    //   title: 'Espere ...',
    //   didOpen: () => {
    //     Swal.showLoading()
    //   },
    //   onAfterClose: () => {
    //     Swal.hideLoading()
    //   },
    //   allowOutsideClick: false,
    //   allowEscapeKey: false,
    //   allowEnterKey: false,
    //   showConfirmButton: false
    // })
    const importe = tipo.value.precio;
    const tipoo = tipo.label.split('-');
    const tipoEntrada = tipoo[0].trim();
    const idtipo = tipo.value._id;
    const arrayCantidad = [];
    const eventoid = idEvento;
    const fecha = moment();
    const usuario = uid;
    const entrada0={numero:cantidadActualEvento+1, dataUrl:dataUrl0};
    const entrada1={numero:cantidadActualEvento+2, dataUrl:dataUrl1};
    const entrada2={numero:cantidadActualEvento+3, dataUrl:dataUrl2};
    const entrada3={numero:cantidadActualEvento+4, dataUrl:dataUrl3};
    const entrada4={numero:cantidadActualEvento+5, dataUrl:dataUrl4};
    const entrada5={numero:cantidadActualEvento+6, dataUrl:dataUrl5};
    const entrada6={numero:cantidadActualEvento+7, dataUrl:dataUrl6};
    const entrada7={numero:cantidadActualEvento+8, dataUrl:dataUrl7};
    const entrada8={numero:cantidadActualEvento+9, dataUrl:dataUrl8};
    const entrada9={numero:cantidadActualEvento+10, dataUrl:dataUrl9};
    switch (cantidad) {
      case 1:
        arrayCantidad.push(entrada0);
        break;  
      case 2:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        break;
      case 3:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        break;
      case 4:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        break;
      case 5:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        break;
      case 6:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        arrayCantidad.push(entrada5);
        break;
      case 7:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        arrayCantidad.push(entrada5);
        arrayCantidad.push(entrada6);
        break;
      case 8:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        arrayCantidad.push(entrada5);
        arrayCantidad.push(entrada6);
        arrayCantidad.push(entrada7);
        break;
      case 9:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        arrayCantidad.push(entrada5);
        arrayCantidad.push(entrada6);
        arrayCantidad.push(entrada7);
        arrayCantidad.push(entrada8);
        break; 
      case 10:
        arrayCantidad.push(entrada0);
        arrayCantidad.push(entrada1);
        arrayCantidad.push(entrada2);
        arrayCantidad.push(entrada3);
        arrayCantidad.push(entrada4);
        arrayCantidad.push(entrada5);
        arrayCantidad.push(entrada6);
        arrayCantidad.push(entrada7);
        arrayCantidad.push(entrada8);
        arrayCantidad.push(entrada9);
        break;              
      default:
        break;
    }
    const body = {arrayCantidad,evento:eventoid,fecha,usuario,importe,nombrevento:evento.nombre,tipo:tipoEntrada,cantidad,idtipo};
    dispatch(pasarClientesPagina(body));
    // Swal.close();
    closeModal();
    setIsOk(true);
  };
  
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyless}
        closeTimeoutMS={200}
        className="modalpay"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      >
      <div className='text-center'>
        {/* {evento&& (evento.ticketMax-evento.ticketGen<=10)?<label className='form-label' style={{color:"white"}}><b style={{color:"lightgreen"}}>{evento.ticketMax-evento.ticketGen>0?`Quedan ${evento.ticketMax-evento.ticketGen} entradas`:'ENTRADAS AGOTADAS'}</b></label>:<label className='form-label' style={{color:"white"}}><b>ENTRADAS</b></label>} */}
        {cantidadActualEvento&&cantidadActualEvento&& (cantidadTotalEvento-cantidadActualEvento<=10)?<label className='form-label' style={{color:"white"}}><b className="text-light">QUEDAN <b className="text-danger">{cantidadTotalEvento-cantidadActualEvento}</b> ENTRADAS</b></label>:<label className='form-label' style={{color:"white"}}><b>ENTRADAS</b></label>}
        <br/>
        {precios&&<div>
           <Select placeholder='Seleccionar...' styles={colourStyles} options={tipos} value={tipo} onChange={handleTipo} isOptionDisabled={(option) => option.disabled} onFocus={handleReload}/>
        </div>}
      </div>
      <hr className="bg-light"/>
      <div style={{color:"white"}} className="d-flex flex-column justify-content-center align-items-center">
        <b style={{fontSize:"20px"}}>CANTIDAD</b>
      </div>
      <div style={{color:"white"}}>
        <button className="me-2 btn btn-dark" onClick={decrementCount}>-</button>

        <span className="p-2">{cantidad}</span>
        
        <button className="ms-2 btn btn-dark" onClick={incrementCount}>+</button>
      </div>
      <div className="mt-4">
        <button onClick={()=>handleGoPagar()} className="btn btn-outline-success btn-block">
          <i className="fa-solid fa-credit-card"></i>
          <span> Pagar</span>
        </button>
      </div>
      {!waitQr&&<QRGeneratorPagina id="canvas0" uid={uid} numero={cantidadActualEvento+1} />}
      {!waitQr&&<QRGeneratorPagina id="canvas1" uid={uid} numero={cantidadActualEvento+2} />}
      {!waitQr&&<QRGeneratorPagina id="canvas2" uid={uid} numero={cantidadActualEvento+3} />}
      {!waitQr&&<QRGeneratorPagina id="canvas3" uid={uid} numero={cantidadActualEvento+4} />}
      {!waitQr&&<QRGeneratorPagina id="canvas4" uid={uid} numero={cantidadActualEvento+5} />}
      {!waitQr&&<QRGeneratorPagina id="canvas5" uid={uid} numero={cantidadActualEvento+6} />}
      {!waitQr&&<QRGeneratorPagina id="canvas6" uid={uid} numero={cantidadActualEvento+7} />}
      {!waitQr&&<QRGeneratorPagina id="canvas7" uid={uid} numero={cantidadActualEvento+8} />}
      {!waitQr&&<QRGeneratorPagina id="canvas8" uid={uid} numero={cantidadActualEvento+9} />}
      {!waitQr&&<QRGeneratorPagina id="canvas9" uid={uid} numero={cantidadActualEvento+10}/>}
      {isOk&&<div className="form-group d-flex justify-content-center">
        <MercadoPagoForm />
      </div>}
      </Modal>
    </div>
  );
};
