import moment from "moment";
import React, { useEffect } from "react";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { fetchSinToken } from "../../helpers/fetch";
import {isMobile} from 'react-device-detect';

export const EventosScreen = () =>  {
  // const navigate = useNavigate();
  const [eventos, setEventos] = useState([]);
  useEffect(() => {
    const getEventos = async() =>{
      const resp = await fetchSinToken("eventos");
      const body = await resp.json();
      setEventos(body.eventos);
    }
    getEventos();
  }, []);
  
  // const handleVolver = (e) => {
  //   navigate('/');
  // };
  return (
    <>
    {isMobile ?
      <div className="container-fluid d-flex flex-column justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
      <div className="d-flex justify-content-center align-items-center">
        <h1 className="text-light mb-2">Eventos</h1>
      </div>
      {eventos && eventos.map((evento,i) => {
      return (
      <div className="">
        <Link className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
            <img src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"300px",height:"350px", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div className="card-body d-flex flex-column justify-content-end align-items-start w-100">
              <h5 className="card-title text-light" style={{fontSize:"20px"}}>{evento.nombre.toUpperCase()}</h5>
              <span className="card-text text-light" style={{fontSize:"15px"}}>{evento.ubicacion.toUpperCase()}</span>
              <span className="card-text text-info" style={{fontSize:"15px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              <span className="card-text text-light pb-4" style={{fontSize:"15px"}}><i className="fa-solid fa-location-dot"></i> Reconquista, Santa Fe</span>
            </div>
          </div>
        </Link>
      </div>
    )})
    }
      {/* <div className="form-group d-flex justify-content-center">
        <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
      </div> */}
    </div>
    :
    <div className="row d-flex justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
      <div className="d-flex justify-content-center align-items-center">
        <h1 className="text-light mb-2">Eventos</h1>
      </div>
      {eventos && eventos.map((evento,i) => {
      return (
      <div className="col-3">
        <Link className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
            <img src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"300px",height:"350px", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div className="card-body d-flex flex-column justify-content-end align-items-start w-100">
              <h5 className="card-title text-light" style={{fontSize:"20px"}}>{evento.nombre.toUpperCase()}</h5>
              <span className="card-text text-light" style={{fontSize:"15px"}}>{evento.ubicacion.toUpperCase()}</span>
              <span className="card-text text-info" style={{fontSize:"15px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              <span className="card-text text-light pb-4" style={{fontSize:"15px"}}><i className="fa-solid fa-location-dot"></i> Reconquista, Santa Fe</span>
            </div>
          </div>
        </Link>
      </div>
    )})
    }
      {/* <div className="form-group d-flex justify-content-center">
        <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
      </div> */}
    </div>
    }
    </>
  );
};
