import React from 'react';
import {isMobile, useDeviceSelectors} from 'react-device-detect';
import { fetchConToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import { useDispatch } from 'react-redux';
import { uiEnviarCelu, uiOpenModal } from '../../actions/ui';
import { CeluModal } from './CeluModal';
import moment from 'moment';

export const CredencialesItems = ({credenciales,loading}) => {
  const dispatch = useDispatch();
  const [selectors, data] = useDeviceSelectors(window.navigator.userAgent);
  if(loading){
    return <h2>Cargando...</h2>
  }
  const handleGoSend = (uiid) => {
    dispatch(uiOpenModal());
    dispatch(uiEnviarCelu(uiid))
  };
  const handleGoSee = (uid) => {
    let windowReference;
    if(data.os.name==="iOS"){
      windowReference = window.open();
    }
    const getPDF = async() => {
      const resp = await fetchConToken(`clientes/getpdf/${uid}`);
      const body = await resp.json();
      if(body.ok){
        // window.open(`${body.url}`)
        if(data.os.name==="iOS"){
          windowReference.location = body.url;
        }else{
          window.open(`${body.url}`)
        }
      }else{
        Swal.fire("Error",body.msg,"error");
      }
    };
    getPDF();
  };
  const handleGoOpen = (uid) => {
    let windowReference;
    if(data.os.name==="iOS"){
      windowReference = window.open();
    }
    const getDownload= async() =>{
      const resp = await fetchConToken(`clientes/getpdf/${uid}`);
      const body = await resp.json();
      if(body.status==="error"){
        Swal.fire('Error',body.message,'error');
      }else{
        const splitComienzo = body.url.split('/',6);
        const splitEntero = body.url.split('/');
        const urll = splitComienzo[0]+"/"+splitComienzo[1]+"/"+splitComienzo[2]+"/"+splitComienzo[3]+"/"+splitComienzo[4]+"/"+splitComienzo[5]+"/fl_attachment/"+splitEntero[6]+"/"+splitEntero[7];
        // window.open(`${urll}`)
        if(data.os.name==="iOS"){
          windowReference.location = urll;
        }else{
          window.open(`${urll}`)
        }
      }
    }
    getDownload();
  };
  return(
  <div className='container-fluid'>
    {credenciales && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"} data-aos="fade-up" data-aos-duration="1000">
    <thead className="bg-dark text-light">
      <tr>
        <th className="text-center small" scope="col">Numero</th>
        <th className="text-center small" scope="col">Evento - Entrada</th>
        <th className="text-center small" scope="col">Fecha</th>
        <th className="text-center small" scope="col">Ver</th>
        <th className="text-center small" scope="col">Descargar</th>
        <th className="text-center small" scope="col">Enviar WSP</th>
      </tr>
   </thead>	
      <>
        {credenciales.length>0&&credenciales.map(credencial => {
          return( 
            <tbody key={credencial.uid}>
             <tr>
                 <td className="text-center small text-light">{ credencial.numero.replace(/\D/g, "") }</td>
                 <td className="text-center small text-light">{ credencial.evento.toUpperCase() } - {credencial.tipo}</td>
                 <td className="text-center small text-light">{ credencial.fecha ? moment(credencial.fecha).format('DD/MM/yyyy'): "-" }</td>
                 <td className="text-center small text-light"><button className='btn btn-light btn-sm' onClick={()=>handleGoSee(credencial.uid)}><i className="fas fa-eye"></i></button></td>
                 <td className="text-center small text-light"><button className='btn btn-primary btn-sm' onClick={()=>handleGoOpen(credencial.uid)}><i className="fas fa-file-arrow-down"></i></button></td>
                 <td className="text-center small text-light"><button className='btn btn-success btn-sm' onClick={()=>handleGoSend(credencial.uid)}><i className="fab fa-whatsapp"></i></button></td>
             </tr>
             <CeluModal evento={credencial.eventoid} />
             </tbody>
          );
          })}
      </>
  </table>}
</div> );
};
