import { useEffect, useState } from "react";
import useScript from "./useScript";
import { formConfig } from "../components/mercadopago/formConfig.js";
import { fetchConToken, fetchMercadoConToken } from "../helpers/fetch";
import { useDispatch, useSelector } from "react-redux";
import { pasarTransaccion } from "../actions/clientes";
// import { fetchConToken } from "../helpers/fetch";

export default function UseMercadoPago() {
    const [resultPayment, setResultPayment] = useState(undefined);
    const { entradas } = useSelector((state) => state.clientes);
    const dispatch = useDispatch();
    let arrayCantidad,cantidad,nombrevento,evento;
    let acctok="";
    let pubkey="";
    evento=entradas.evento;
    acctok=entradas.acctok;
    pubkey=entradas.pubkey;
    arrayCantidad = entradas.arrayCantidad;
    cantidad = entradas.cantidad;
    nombrevento = entradas.nombrevento;
    const { MercadoPago } = useScript(
        "https://sdk.mercadopago.com/js/v2",
        "MercadoPago"
    );
    //MERCADOPAGO CHECKOUT PRO
    useEffect(() => {
        if (MercadoPago) {
        const mp = new MercadoPago(pubkey, {
            locale: 'es-AR'
        });
        const createPreference = async()=>{
            const resp = await fetchConToken(
                "mercadopago/preference",
                { nombrevento,cantidad, arrayCantidad,acctok,evento},
                "POST"
              );
            const body = await resp.json();
            dispatch(pasarTransaccion(entradas,body.id))
            const checkout = mp.checkout({
                preference: {
                    id: body.id
                },
                autoOpen: true,
                theme: {
                    elementsColor: '#050505',
                }
            });
            setResultPayment(body);
          }
          createPreference();
        }
    }, [MercadoPago,nombrevento,cantidad,arrayCantidad,dispatch,entradas,acctok,pubkey,evento])
    return resultPayment;
}
