import { useNavigate, useLocation } from "react-router-dom";
import queryString from 'query-string';
// import { crearClientePagina } from "../../actions/clientes";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { pasaMp } from "../../actions/clientes";

export const Pagos = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  let params = queryString.parse(location.search);
  const preferenceId=params.preference_id?params.preference_id:"";
  const status=params.status?params.status:"";
  const pagoid = params.payment_id?params.payment_id:"";
  const corte = preferenceId.split('-');
  const vendedorid = corte[0];
  // const [entradas, setEntradas] = useState(null)
  useEffect(() => {
    // const startRegisterClientes = async() => {
    //   const resp = await fetchConToken(`clientes/getransaccion/${preferenceId}`);
    //   const body = await resp.json();
    //   if(body.ok){
    //     setEntradas(body.entradas)
    //   }else{
    //     Swal.fire("Error", body.msg, "error");
    //   }
    // }
    // startRegisterClientes();
    if(preferenceId!=="" && status!=="" && pagoid!=="" && vendedorid!==""){
      dispatch(pasaMp(preferenceId,status,pagoid,vendedorid));
      navigate('/finalizar');
    }
    // setTimeout(()=>{navigate('/finalizar');},1000)
    
    
  }, [dispatch,navigate,preferenceId,status,pagoid,vendedorid])
  return(
    <div className="d-flex justify-content-center align-items-center">
    <h1>REDIRECCIONANDO...</h1>
    </div>
)};
