import React, { useState } from "react";
import {Link, Outlet } from "react-router-dom";
import { Navbar } from "../components/ui/Navbar";
import { Footer } from "../components/ui/Footer";

export const Layout = () => {
    // let location = useLocation();
    // localStorage.setItem('lastPath', location.pathname);
    const [texto, setTexto] = useState('')
    
    return (
        <>
        <div className="d-flex justify-content-end align-items-center p-3 fixed-bottom">
            <Link to={'/misentradas'}><button className={`btn ${texto}`} style={{color:"white"}} onMouseEnter={()=>{setTexto('border border-light')}} onMouseLeave={()=>{setTexto('')}}><i className="fa-solid fa-eye"></i> VER MIS ENTRADAS COMPRADAS</button></Link>
        </div>
            <Navbar/>
            <Outlet/>
            <Footer/>
        </>
    );
}