import moment from 'moment';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { fetchSinToken } from '../../helpers/fetch';

export const Carousel = () => {
  const [eventos, setEventos] = useState([]);
  useEffect(() => {
    const getEventos = async() =>{
      const resp = await fetchSinToken("eventos");
      const body = await resp.json();
      setEventos(body.eventos);
    }
    getEventos();
    //PARA AUTOCICLAR PORQUE SINO CLICKEAS NO FUNCIONA EL AUTOCYCLING
    setTimeout(()=>{
      if(document.getElementById('apreta')){
        document.getElementById('apreta').click();
      }
    },4000);
  }, []);
  return (
  <div id="myCarousel" className="carousel slide carousel-fade mb-3" data-bs-ride="carousel" data-bs-pause="false" data-bs-interval="4000" data-aos="fade-up" data-aos-duration="1000">
    <h1>Prximos eventos</h1>
    
    <div className="carousel-indicators">
    {eventos && eventos.map((evento,i) => {
      return (
        <div key={evento.id}>
          {i === 0
          ?
          <button type="button" data-bs-target="#myCarousel" data-bs-slide-to={i} className="active" aria-current="true" aria-label="Slide 1"></button>
          :
          <button type="button" data-bs-target="#myCarousel" data-bs-slide-to={i} aria-current="true" aria-label={`Slide ${i}`}></button>
          }
        </div>
      )})
    }
    </div>
    
    <div className="carousel-inner">
    {eventos && eventos.map((evento,i) => {
      return (
      i === 0
      ? <div className="carousel-item active" key={evento.id} > 
      <div className="d-flex justify-content-center">
        <Link  className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-black" style={{width: "400px",height:"450px", borderRadius:"inherit", border:"1px solid black"}}>
            <img src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"100%",height:"100%", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div className="card-body d-flex flex-column justify-content-end align-items-start position-absolute box w-100">
              <h5 className="card-title text-light" style={{fontSize:"30px"}}>{evento.nombre}</h5>
              <span className="card-text text-light" style={{fontSize:"20px"}}>{evento.ubicacion}</span>
              <span className="card-text text-info pb-4" style={{fontSize:"20px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              {/* <span className="card-text text-light pb-4" style={{fontSize:"20px"}}><i className="fa-solid fa-location-dot"></i> {evento.direccion}</span> */}
            </div>
          </div>
        </Link>
      </div>
    </div>
      : <div className="carousel-item" key={evento.id}>
      <div className="d-flex justify-content-center">
        <Link className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-dark" style={{width: "400px",height:"450px", borderRadius:"inherit", border:"1px solid black"}}>
            <img  src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"100%",height:"100%", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div  className="card-body d-flex flex-column justify-content-end align-items-start position-absolute box w-100">
              <h5 className="card-title text-light" style={{fontSize:"30px"}}>{evento.nombre}</h5>
              <span className="card-text text-light" style={{fontSize:"20px"}}>{evento.ubicacion}</span>
              <span className="card-text text-info" style={{fontSize:"20px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              <span className="card-text text-light pb-4" style={{fontSize:"20px"}}><i className="fa-solid fa-location-dot"></i> Reconquista, Santa Fe</span>
            </div>
          </div>
        </Link>
      </div>
    </div>
    )})
    }
    </div>
    <button aria-label="Anterior" className="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
      <span className="carousel-control-prev-icon" aria-hidden="true" />
    </button>
    <button id="apreta" aria-label="Siguiente" className="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
      <span className="carousel-control-next-icon" aria-hidden="true" />
    </button>
  </div>
);
}
