import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, matchRoutes, useLocation, useNavigate } from "react-router-dom";
import { startLogout, startSetRrppActive } from "../../actions/auth";
import { eventLogout, eventosLoaded2 } from "../../actions/eventos";
import logo from '../../images/dame.png';
import {isMobile} from 'react-device-detect';
import { useForm } from "../../hooks/useForm";
import { fetchConToken } from "../../helpers/fetch";
import { googleLogout } from '@react-oauth/google';

export const Navbar = () => {
  const { nombre,rol,uid} = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  // console.log(location.pathname)
  // location.pathname('/busqueda');
  const [isAuthed, setIsAuthed] = useState(false);
  useEffect(() => {
    if(rol){
      setIsAuthed(true);
    }
  }, [setIsAuthed,rol]);
  const handleUserAsoc = ()=>{
    if(isAuthed){
      dispatch(startSetRrppActive(uid));
    }
  }  
  const handleLogout = () => {
    googleLogout();
    dispatch(startLogout());
    dispatch(eventLogout());
  };
  const [formSearchValues, handleSearchInputChange] = useForm({
    cSearch:""
  });
  const { cSearch } = formSearchValues;
  const handleBuscar = (e) => {
    e.preventDefault();
    const search = async() =>{
      const resp = await fetchConToken(`eventos/busqueda/${cSearch}`);
      const body = await resp.json();
      dispatch(eventosLoaded2(body.eventos));
      navigate('/busqueda');
    }
    search();
  };
  return (
    <>
      { isMobile ?
      <div className="navbar navbar-dark navbar-expand-lg d-flex justify-content-between align-items-center mb-1">
      <Link reloadDocument className="text-decoration-none" to="/"><span role="button" className="navbar-brand ms-4 text-decoration-none"><img style={{objectFit:"cover"}} width={150} height={75} src={logo} alt="Event App" /></span></Link>
      <button type="button" className="navbar-toggler me-5" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
        <span className="navbar-toggler-icon"></span>
      </button>
      
      <div id="navbarCollapse" className="me-5 collapse navbar-collapse" style={{textAlign:"center"}}>
        <div className="navbar-nav ms-auto">
        <Link reloadDocument className="text-decoration-none m-1" to="/eventos"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>EVENTOS</strong></Link>
        {/* <Link reloadDocument className="text-decoration-none" to="/location"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Ubicacion</strong></Link> */}
        <Link reloadDocument className="text-decoration-none m-1" to="/fotos"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>FOTOS</strong></Link>
        {/* <Link className="text-decoration-none" to="/about"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Nosotros</strong></Link> */}
        {isAuthed
        ?
          <div className="dropdown ms-4 text-decoration-none m-1">
            <strong style={{ fontSize:"18px" }} className="navbar-brand dropdown-toggle text-decoration-none ps-3" href="!#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">{nombre}</strong>
            <ul className="dropdown-menu dropdown-menu-end dropdown-pull-right rounded-1" style={{ backgroundColor:"#000", textAlign:"center"}} aria-labelledby="userDropdown">
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/misdatos">Mis datos</Link></li>
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/misentradas">Mis entradas</Link></li>
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/miscredenciales">Mis credenciales</Link></li>
              <li><button onClick={handleLogout} className="dropdown-item dropdown-item-danger rounded-1" style={{ color:"white" }}>Cerrar sesion</button></li>
            </ul>
          </div>
        : 
        <div className="dropdown ms-4 text-decoration-none m-1">
          <strong style={{ fontSize:"18px" }} className="navbar-brand dropdown-toggle text-decoration-none" href="!#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">Ingresar</strong>
          <ul className="dropdown-menu dropdown-pull-right rounded-1" style={{ backgroundColor:"#000", textAlign:"center" }} aria-labelledby="userDropdown">
            <li><Link className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/login">Iniciar sesion</Link></li>
            <li><Link className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/register">Registrarse</Link></li>
          </ul>
        </div>
        }
        </div>
      </div>
      <div className="p-2 w-100">
        {location.pathname!=="/busqueda"&&<div className="input-group ">
          <input className="form-control" placeholder="Buscar evento..." type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
          <button onClick={handleBuscar} type="button" className="input-group-text btn btn-dark">
            <i className="fa-solid fa-magnifying-glass"></i>
          </button>
        </div>}
      </div>
      </div>
    :
    <div className="navbar navbar-dark navbar-expand-lg d-flex justify-content-between align-items-center mb-1">
    <Link reloadDocument className="text-decoration-none" to="/"><span role="button" className="navbar-brand ms-4 text-decoration-none"><img style={{objectFit:"cover"}} width={150} height={75} src={logo} alt="Event App" /></span></Link>
      <button type="button" className="navbar-toggler me-5" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
        <span className="navbar-toggler-icon"></span>
      </button>
      
      {location.pathname!=="/busqueda"&&<div className="input-group">
        <input className="form-control" placeholder="Buscar evento..." type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
        <button onClick={handleBuscar} type="button" className="input-group-text btn btn-dark">
          <i className="fa-solid fa-magnifying-glass"></i>
        </button>
      </div>}
      <div id="navbarCollapse" className="me-5 collapse navbar-collapse">
        <div className="navbar-nav ms-auto">
        <Link reloadDocument className="text-decoration-none" to="/eventos"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Eventos</strong></Link>
        {/* <Link reloadDocument className="text-decoration-none" to="/location"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Ubicacion</strong></Link> */}
        <Link reloadDocument className="text-decoration-none" to="/fotos"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Fotos</strong></Link>
        {/* <Link className="text-decoration-none" to="/about"><strong role="button" className="navbar-brand ms-4 text-decoration-none" style={{fontSize:"18px"}}>Nosotros</strong></Link> */}
        {isAuthed
        ?
          <div className="dropdown ms-4 text-decoration-none">
            <strong style={{ fontSize:"18px" }} className="navbar-brand dropdown-toggle text-decoration-none" href="!#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">{nombre}</strong>
            <ul className="dropdown-menu dropdown-menu-end dropdown-pull-right rounded-1" style={{ backgroundColor:"#000" }} aria-labelledby="userDropdown">
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/misdatos">Mis datos</Link></li>
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/misentradas">Mis entradas</Link></li>
              <li><Link onClick={handleUserAsoc} className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/miscredenciales">Mis credenciales</Link></li>
              <li><button onClick={handleLogout} className="dropdown-item dropdown-item-danger rounded-1" style={{ color:"white" }}>Cerrar sesion</button></li>
            </ul>
          </div>
        : 
        <div className="dropdown ms-4 text-decoration-none">
          <strong style={{ fontSize:"18px" }} className="navbar-brand dropdown-toggle text-decoration-none" href="!#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">Ingresar</strong>
          <ul className="dropdown-menu dropdown-pull-right rounded-1" style={{ backgroundColor:"#000" }} aria-labelledby="userDropdown">
            <li><Link className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/login">Iniciar sesion</Link></li>
            <li><Link className="text-decoration-none dropdown-item dropdown-item-default rounded-1" style={{ color:"white" }} to="/register">Registrarse</Link></li>
          </ul>
        </div>
        }
        </div>
      </div>
      </div>
    }
    </>
  );
};
