import React from "react";
import { useState } from "react";
import { fetchConToken} from "../../helpers/fetch";
import { useSelector } from "react-redux";
import moment from "moment";
import {isMobile} from 'react-device-detect';

export const LikeSect = ({foto,evento,likess,likee}) =>  {
  let liker;
  for (let i = 0; i < likee.length; i++) {
    if(likee[i]!==undefined){
      liker = likee[i];
      break;
    }
  }
  const [like, setLike] = useState(liker);
  // const [like, setLike] = useState(likee[0]||likee[1]);
  const [likes, setLikes] = useState(likess);
  const { uid,nombre } = useSelector((state) => state.auth);
  const handleLike = (foto) => {
    setLike(!like);
    const eventoid = evento.id;
    const nombrevento = evento.nombre;
    const ubicacion = evento.ubicacion;
    const fecha = moment(evento.fecha).format('DD/MM/YYYY');
    const addLike = async() =>{
      const resp = await fetchConToken("eventos/addlike",{foto,like:!like,uid,fecha,ubicacion,nombrevento,eventoid,nombre},"POST");
      const body = await resp.json();
      if(body.ok){
        setLikes(body.likes);
      }
    }
    addLike();
  };
  return (
    <>
    { isMobile ?
    <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
    <img loading="lazy" src={foto.foto ? foto.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"cover",width:"320px",height:"210px", borderRadius:"inherit", border:"1px solid black"}} alt="foto"/>
    {uid&&<div className="card-body d-flex flex-row justify-content-start align-items-center position-absolute w-100">
      <div className="card-title d-flex flex-column justify-content-center align-items-center text-light me-3">
      <span className="card-text text-light" style={{fontSize:"25px"}}>{likes}</span>
      <input type='checkbox' id={foto.id} className="d-none" value={like} onChange={()=>{handleLike(foto.id)}} style={{fontSize:"30px"}} />
      <label htmlFor={foto.id}>{like?<i className="fa-solid fa-heart fa-xl text-danger"></i>:<i className="fa-regular fa-heart fa-xl text-danger"></i>}</label>
      </div>
      <a href={'https://wa.me/?text='+foto.foto} target='_blank' rel="noopener noreferrer"><i className="fa-solid fa-xl fa-share"></i></a>
      </div>}
  </div>
    :
    <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
      <img loading="lazy" src={foto.foto ? foto.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"600px",height:"400px", borderRadius:"inherit", border:"1px solid black"}} alt="foto"/>
      {uid&&<div className="card-body d-flex flex-row justify-content-start align-items-center position-absolute w-100">
        <div className="card-title d-flex flex-column justify-content-center align-items-center text-light me-3">
        <span className="card-text text-light" style={{fontSize:"30px"}}>{likes}</span>
        <input type='checkbox' id={foto.id} className="d-none" value={like} onChange={()=>{handleLike(foto.id)}} style={{fontSize:"30px"}} />
        <label htmlFor={foto.id}>{like?<i className="fa-solid fa-heart fa-2xl text-danger"></i>:<i className="fa-regular fa-heart fa-2xl text-danger"></i>}</label>
        </div>
        <a href={'https://wa.me/?text='+foto.foto} target='_blank' rel="noopener noreferrer"><i className="fa-solid fa-share fa-2xl"></i></a>
        </div>}
    </div>
  }
  </>
  );
};
