import React from 'react';
import { isMobile, useDeviceSelectors } from 'react-device-detect';
import { fetchConToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import moment from 'moment';

export const EntradasHistorialItems = ({ entradas, loading }) => {
  const [selectors, data] = useDeviceSelectors(window.navigator.userAgent);
  if (loading) {
    return <h2>Cargando...</h2>
  }
  const handleGoSee = (uid) => {
    let windowReference;
    if (data.os.name === "iOS") {
      windowReference = window.open();
    }
    const getPDF = async () => {
      const resp = await fetchConToken(`clientes/getpdf/${uid}`);
      const body = await resp.json();
      if (body.ok) {
        if (data.os.name === "iOS") {
          windowReference.location = body.url;
        } else {
          window.open(`${body.url}`)
        }
      } else {
        Swal.fire("Error", body.msg, "error");
      }
    };
    getPDF();
  };
  return (
    <div className='container-fluid'>
      {entradas && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped" : "table table-bordered table-striped"} data-aos="fade-up" data-aos-duration="1000">
        <thead className="bg-dark text-light">
          <tr>
            <th className="text-center small" scope="col">Numero</th>
            <th className="text-center small" scope="col">Evento - Entrada</th>
            <th className="text-center small" scope="col">Fecha</th>
            <th className="text-center small" scope="col">Ver</th>
          </tr>
        </thead>
        <>
          {entradas.length > 0 && entradas.map(entrada => {
            return (
              <tbody key={entrada.uid}>
                <tr>
                  <td className="text-center small text-light">{entrada.numero.replace(/\D/g, "")}</td>
                  <td className="text-center small text-light">{entrada.evento.toUpperCase()} - {entrada.tipo}</td>
                  <td className="text-center small text-light">{entrada.fecha ? moment(entrada.fecha).format('DD/MM/yyyy') : "-"}</td>
                  {entrada.pagado ? <td className="text-center small text-light"><button className='btn btn-light btn-sm' onClick={() => handleGoSee(entrada.uid)}><i className="fas fa-eye"></i></button></td> : <td className="text-center small text-light">-</td>}
                </tr>
              </tbody>
            );
          })}
        </>
      </table>}
    </div>);
};
