import React, { useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { fetchConToken } from "../../helpers/fetch";
import { PaginatedCredItems } from "./PaginatedCredItems";

export const CredencialesScreen = () =>  {
  const { uid } = useSelector((state) => state.auth);
  const navigate = useNavigate();
  const [credenciales, setCredenciales] = useState([]);
  const [loading, setLoading] = useState(false);
  //FETCHING
  useEffect(() => {
    const getCredenciales = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/paginacred/${uid}`);
      const body = await resp.json();
      setCredenciales(body.credenciales);
      setLoading(false);
    }
    getCredenciales();
  }, [uid]);
  const handleVolver = (e) => {
    navigate('/');
  };
  return (
    <div className="d-flex flex-column justify-content-center align-items-center text-center">
      <h1 className="text-light mb-2">Mis credenciales</h1>
      {credenciales.length>0&&<h5 className="text-light mb-2">Click en la tabla para desplegar y ver las credenciales</h5>}
      <hr style={{color:"white"}}/>

      {credenciales.length>0?
      <PaginatedCredItems itemsPerPage={10} items={credenciales} loading={loading}/>
      :
      <span className="h3 text-light">No hay credenciales</span>}
      <div className="form-group d-flex justify-content-center">
        <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
      </div>
    </div>
  );
};
