import React from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import { uiCloseModall } from "../../actions/ui";
import "../modal.css";
import { startClearClienteAsoc } from "../../actions/clientes";
import { useNavigate } from "react-router-dom";
import { fetchConToken } from "../../helpers/fetch";
import Swal from "sweetalert2";
import { useForm } from "../../hooks/useForm";
import { useCallback } from "react";
import { QRGeneratorPaginaCred } from "../ui/QRGeneratorPaginaCred";
import { useState } from "react";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const CanjearModal = ({evento}) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { uid } = useSelector((state) => state.auth);
  const { modalOpenn } = useSelector((state) => state.ui);
  const [formCodigoValues, handleCodigoInputChange] = useForm({
    eCodigo:"",
  });
  const { eCodigo } = formCodigoValues;
  const [numeroFree, setNumeroFree] = useState(0);
  const qrCodeCanvas0 = document.querySelector('#canvas0');
  const dataUrl0 = qrCodeCanvas0&&qrCodeCanvas0.toDataURL('image/jpg', 0.3);

  const closeModall = useCallback(() => {
    dispatch(uiCloseModall());
    dispatch(startClearClienteAsoc());
  },[dispatch]);

  const handleGoCanjear = async(e) => {
      e.preventDefault();
      if (eCodigo === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar el campo",
          "error"
        );
      }
      const resp = await fetchConToken(
        "clientes/ingresofree",
        { id:eCodigo,eventoid:evento.id },
        "POST"
      );
      const body = await resp.json();
      setNumeroFree(body.numero);
      if(body.ok){
        const startRegisterClientes = async(numero,bodi) => {
          const eventoid = evento.id;
          const usuario = uid;
          const id = bodi.credencial.uid;
          const cliente0={numero, dataUrl:dataUrl0};
            const respu = await fetchConToken(
              "clientes/newclientesfree",
              { arrayCantidad:cliente0,eventoid,usuario,id},
              "POST"
            );
            const bode = await respu.json();
            if (bode.ok) {
              const fileUpload = async(numero,evento)=>{
                const respa = await fetchConToken(
                  "clientes/fileuploadfree",
                  { numero,evento },
                  "POST"
                );
                const bodc = await respa.json();
                if(bodc.ok){
                  Swal.fire("xito", "Ticket canjeado con xito", "success");
                  navigate('/miscredenciales');
                }else{
                  Swal.fire("Error", bodc.msg, "error");
                }
              }
              fileUpload(bode.entrada.numero,bode.entrada.eventoid);
              // setTimeout(()=>{fileUpload(bode.entrada.numero,bode.entrada.evento)},1000);
            } else {
              Swal.fire("Error", bode.msg, "error");
            }
        };   
        startRegisterClientes(numeroFree,body); 
        closeModall();
      }else{
        Swal.fire("Error", body.msg, "error");
      }
  };

  
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpenn}
        onRequestClose={closeModall}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      >
      <div style={{color:"white"}} className="d-flex flex-column justify-content-center align-items-center">
        <b style={{fontSize:"25px"}}>Canjear codigo</b>
      </div>
        <hr />
        <div style={{color:"white"}} className="mt-3 d-flex flex-column justify-content-center align-items-center">
        <form onSubmit={handleGoCanjear}>
          <div className="form-group">
            <input
              type="text"
              className="form-control mb-2 text-center"
              placeholder="Ingrese cdigo"
              name="eCodigo"
              value={eCodigo}
              onChange={handleCodigoInputChange}
            />
          </div>
          <div className="form-group d-flex justify-content-center">
            <input type="submit" className="btn mt-2 btn-success" value="Canjear" />
          </div>
        </form>
      </div>
      <QRGeneratorPaginaCred id="canvas0" uid={uid} numero={numeroFree} />
      </Modal>
    </div>
  );
};
