import mercado from '../../images/mercadopago.png';
import master from '../../images/master.png';
import visa from '../../images/visa.png';

export const Banner2 = () => (
  <div  className="d-flex flex-column align-items-center justify-content-center bg-dark" style={{color:"white"}} data-aos="fade-up" data-aos-duration="1000">
    <div className="container-fluid p-2">
            <strong style={{fontSize:"20px"}}>PAGOS ONLINE</strong>
            <div className='mb-2'>
              <img src={visa} style={{objectFit:"cover",width:"64px",height:"64px"}} className="me-5" alt="visa"/>
              {' '}
              <img src={mercado} style={{objectFit:"cover",width:"64px",height:"64px"}} className="me-5" alt="mercadopago"/>
              {' '}
              <img src={master} style={{objectFit:"cover",width:"64px",height:"64px"}} alt="mastercard"/>
            </div>
      </div>
    </div>
);
