import moment from "moment";
import React from "react";
import { Link, useNavigate } from "react-router-dom";
import {isMobile} from 'react-device-detect';
import { useDispatch, useSelector } from "react-redux";
import { fetchConToken, fetchSinToken } from "../../helpers/fetch";
import { eventosLoaded2 } from "../../actions/eventos";
import { useForm } from "../../hooks/useForm";
import { useState } from "react";

export const EventosBuscados = () =>  {
  const { eventosBusqueda } = useSelector((state) => state.eventos);
  const dispatch = useDispatch();
  // const [formSearchValues, handleSearchInputChange] = useForm({
  //   cSearch:""
  // });
  // const { cSearch } = formSearchValues;
  const [values, setValues] = useState({cSearch:""});
  const handleSearchInputChange = ({ target }) => {
    const getEventos = async() =>{
      const resp = await fetchSinToken("eventos");
      const body = await resp.json();
      dispatch(eventosLoaded2(body.eventos));
    }
    
    const search = async() =>{
      const resp = await fetchConToken(`eventos/busqueda/${cSearch}`);
      const body = await resp.json();
      dispatch(eventosLoaded2(body.eventos));
    }
      setValues({
      ...values,
      [target.name]: target.value,
      });
      if(target.value===""){
        getEventos();
      }else{
        search()
      }
  };
  const { cSearch } = values;
  const handleBuscar = (e) => {
    e.preventDefault();
    const search = async() =>{
      const resp = await fetchConToken(`eventos/busqueda/${cSearch}`);
      const body = await resp.json();
      dispatch(eventosLoaded2(body.eventos));
    }
    search();
  };
  return (
    <>
    {isMobile ?
      <div className="container-fluid d-flex flex-column justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
      <div className="d-flex flex-column justify-content-center align-items-center">
        <h1 className="text-light mb-2">Eventos</h1>
        <div className="input-group">
          <input className="form-control" placeholder="Buscar evento..." type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
          <button onClick={handleBuscar} type="button" className="input-group-text btn btn-dark">
            <i className="fa-solid fa-magnifying-glass"></i>
          </button>
        </div>
      </div>
      {eventosBusqueda && eventosBusqueda.map((evento,i) => {
      return (
      <div className="">
        <Link className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
            <img src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"300px",height:"350px", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div className="card-body d-flex flex-column justify-content-end align-items-start w-100">
              <h5 className="card-title text-light" style={{fontSize:"20px"}}>{evento.nombre.toUpperCase()}</h5>
              <span className="card-text text-light" style={{fontSize:"15px"}}>{evento.ubicacion.toUpperCase()}</span>
              <span className="card-text text-info" style={{fontSize:"15px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              <span className="card-text text-light pb-4" style={{fontSize:"15px"}}><i className="fa-solid fa-location-dot"></i> Reconquista, Santa Fe</span>
            </div>
          </div>
        </Link>
      </div>
    )})
    }
      {/* <div className="form-group d-flex justify-content-center">
        <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
      </div> */}
    </div>
    :
    <div className="row d-flex justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
      <div className="d-flex flex-column justify-content-center align-items-center">
        <h1 className="text-light mb-2">Eventos</h1>
        <div className="input-group w-25">
        <input className="form-control" placeholder="Buscar evento..." type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
        <button onClick={handleBuscar} type="button" className="input-group-text btn btn-dark">
          <i className="fa-solid fa-magnifying-glass"></i>
        </button>
      </div>
      </div>
      
      {eventosBusqueda && eventosBusqueda.map((evento,i) => {
      return (
      <div key={evento._id} className="col-3">
        <Link className='text-decoration-none' to={`/evento/${evento.id}`} style={{overflow:"hidden",borderRadius:"2%", border:"1px solid black"}}>
          <div className="card justify-content-end bg-black" style={{ borderRadius:"inherit", border:"1px solid black"}}>
            <img src={evento.foto ? evento.foto:`https://res.cloudinary.com/dszsrf58b/image/upload/v1664987886/foto_ygsnec.jpg`} style={{objectFit:"fill",width:"300px",height:"350px", borderRadius:"inherit", border:"1px solid black"}} alt="evento"/>
            <div className="card-body d-flex flex-column justify-content-end align-items-start w-100">
              <h5 className="card-title text-light" style={{fontSize:"20px"}}>{evento.nombre.toUpperCase()}</h5>
              <span className="card-text text-light" style={{fontSize:"15px"}}>{evento.ubicacion.toUpperCase()}</span>
              <span className="card-text text-info" style={{fontSize:"15px"}}>{moment(evento.fecha).format('DD/MM/YYYY')}</span>
              <span className="card-text text-light pb-4" style={{fontSize:"15px"}}><i className="fa-solid fa-location-dot"></i> Reconquista, Santa Fe</span>
            </div>
          </div>
        </Link>
      </div>
    )})
    }
    </div>
    }
    </>
  );
};
