import React from 'react';
import {isMobile, useDeviceSelectors} from 'react-device-detect';
import { fetchConToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import moment from 'moment';
import { CeluModal } from './CeluModal';
import {  useDispatch } from 'react-redux';
import { uiEnviarCelu, uiOpenModal } from '../../actions/ui';

export const EntradasItems = ({entradas,loading}) => {
  const dispatch = useDispatch();
  const [selectors, data] = useDeviceSelectors(window.navigator.userAgent);
  if(loading){
    return <h2>Cargando...</h2>
  }
  const handleGoSend = (uiid) => {
    dispatch(uiOpenModal());
    dispatch(uiEnviarCelu(uiid))
  };
  
  const handleGoSee = (uid) => {
    let windowReference;
    if(data.os.name==="iOS"){
      windowReference = window.open();
    }
    const getPDF = async() => {
      const resp = await fetchConToken(`clientes/getpdf/${uid}`);
      const body = await resp.json();
      if(body.ok){
        if(data.os.name==="iOS"){
          windowReference.location = body.url;
        }else{
          window.open(`${body.url}`)
        }
      }else{
        Swal.fire("Error",body.msg,"error");
      }
    };
    getPDF();
  };
  const handleGoOpen = (uid) => {
    let windowReference;
    if(data.os.name==="iOS"){
      windowReference = window.open();
    }
    const getDownload= async() =>{
      const resp = await fetchConToken(`clientes/getpdf/${uid}`);
      const body = await resp.json();
      if(body.status==="error"){
        Swal.fire('Error',body.message,'error');
      }else{
        const splitComienzo = body.url.split('/',6);
        const splitEntero = body.url.split('/');
        const urll = splitComienzo[0]+"/"+splitComienzo[1]+"/"+splitComienzo[2]+"/"+splitComienzo[3]+"/"+splitComienzo[4]+"/"+splitComienzo[5]+"/fl_attachment/"+splitEntero[6]+"/"+splitEntero[7];
        if(data.os.name==="iOS"){
          windowReference.location = urll;
        }else{
          window.open(`${urll}`)
        }
      }
    }
    getDownload();
  };
  const handleGoVerify = (uid,pagoid,eventoid,numero,tipo,importe) => {
    
    const getVerify= async() =>{
      const resp = await fetchConToken(`auth/getmp/${eventoid}`);
      const body = await resp.json();
      if(body.ok){
        const respa = await fetch(`https://api.mercadopago.com/v1/payments/${pagoid}`,{
          method:'GET',
          headers: {'Authorization':`Bearer ${body.acctok}`}
        })
        const bodi = await respa.json();
        if(bodi.status){
          if(bodi.status==="approved"){
            const respc = await fetchConToken(`clientes/actualizarclientes`,{numero},'POST');
            const bodyc = await respc.json(); 
            if(bodyc.ok){
              Swal.fire('xito','Entrada verificada con xito','success')
            }
          }else if(bodi.status==="in_process"){
            Swal.fire('Espere...','Pago an en proceso, reintente ms tarde','warning')
          }else if(bodi.status==="rejected"){
            const respc = await fetchConToken(`clientes/rejectedentrada`,{numero,eventoid,tipo,importe},'POST');
            const bodyc = await respc.json(); 
            if(bodyc.ok){
              Swal.fire('Error','Pago rechazado, entrada eliminada','error')
            }
          }else if(bodi.status==="cancelled"){
            Swal.fire('Error','Pago cancelado, El plazo de pago expiro o fue cancelado por alguna de las partes','error')
          }else if(bodi.status==="refunded"){
            Swal.fire('Error','Pago reintegrado','error')
          }
        }
        // console.log(bodi.status)
      }
    }
    getVerify();
  };
  return(
  <div className='container-fluid'>
    {entradas && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"} data-aos="fade-up" data-aos-duration="1000">
    <thead className="bg-dark text-light">
      <tr>
      <th className="text-center small" scope="col">Numero</th>
       <th className="text-center small" scope="col">Evento - Entrada</th>
       <th className="text-center small" scope="col">Fecha</th>
       <th className="text-center small" scope="col">Estado de pago</th>
       <th className="text-center small" scope="col">Ver</th>
       <th className="text-center small" scope="col">Descargar</th>
       <th className="text-center small" scope="col">Enviar WSP</th>
      </tr>
   </thead>	
      <>
        {entradas.length>0&&entradas.map(entrada => {
          return( 
            <tbody key={entrada.uid}>
             <tr>
              <td className="text-center small text-light">{ entrada.numero.replace(/\D/g, "") }</td>
                 <td className="text-center small text-light">{ entrada.evento.toUpperCase() } - {entrada.tipo}</td>
                 <td className="text-center small text-light">{ entrada.fecha ? moment(entrada.fecha).format('DD/MM/yyyy'): "-" }</td>
                 <td className="text-center small text-light">{ entrada.pagado ? <i className="text-success fa-solid fa-check"></i>:<div className='d-flex flex-column justify-content-center align-items-center'><i className="text-danger fa-solid fa-xmark"></i><button className='btn btn-warning btn-sm' onClick={()=>handleGoVerify(entrada.uid,entrada.pagoid,entrada.eventoid,entrada.numero,entrada.tipo,entrada.importe)}>Verificar pago</button> </div>}</td>
                 {entrada.pagado?<td className="text-center small text-light"><button className='btn btn-light btn-sm' onClick={()=>handleGoSee(entrada.uid)}><i className="fas fa-eye"></i></button></td>:<td className="text-center small text-light">-</td>}
                 {entrada.pagado?<td className="text-center small text-light"><button className='btn btn-primary btn-sm' onClick={()=>handleGoOpen(entrada.uid)}><i className="fas fa-file-arrow-down"></i></button></td>:<td className="text-center small text-light">-</td>}
                 {entrada.pagado?<td className="text-center small text-light"><button className='btn btn-success btn-sm' onClick={()=>handleGoSend(entrada.uid)}><i className="fab fa-whatsapp"></i></button></td>:<td className="text-center small text-light">-</td>}
             </tr>
             <CeluModal evento={entrada.eventoid} />
             </tbody>
          );})}
      </>
  </table>}
</div>);
};
