import React from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { startUpdateUsuario } from "../../actions/auth";
import { useForm } from "../../hooks/useForm";

export const PerfilScreen = () => {
  const { userAsoc } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isOculto, setIsOculto] = useState(true);
  const [formUsuarioValues, handleUsuarioInputChange] = useForm({
    rNombre:userAsoc ? userAsoc.nombre: "",
    rApellido:userAsoc ? userAsoc.apellido: "",
    rEmail: userAsoc ? userAsoc.email:"",
    rPassword:userAsoc ? userAsoc.password: "",
  });

  const { rNombre, rApellido, rPassword, rEmail } = formUsuarioValues;
  
  const handleUpdate = (e) => {
    e.preventDefault();
    if ( rNombre === "" || rApellido === "" || rPassword === "" || rEmail === "") {
      return Swal.fire(
        "Error",
        "Debes de rellenar todos los campos",
        "error"
      );
    }
    dispatch(startUpdateUsuario( rEmail,rPassword, rNombre, rApellido));
    setTimeout(()=>{
      navigate('/');
    },500);
  };
  const handleVolver = (e) => {
    navigate('/');
  };
  const setOculto = () => {
    if(isOculto){
      setIsOculto(false);
    }else{
      setIsOculto(true);
    }
  };
  return (
    userAsoc &&<div className="d-flex flex-column justify-content-center align-items-center">
      
      <h1 className="text-light mb-2">Mis datos</h1>

      <hr style={{color:"white"}}/>
      <form onSubmit={handleUpdate} data-aos="fade-up" data-aos-duration="1000">
    <div className="form-group text-center">
      <label style={{color:"white"}}>Nombre</label>
      <input
        type="text"
        className="form-control mb-2 bg-dark text-light"
        placeholder="Nombre"
        name="rNombre"
        value={rNombre}
        onChange={handleUsuarioInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Apellido</label>
      <input
        type="text"
        className="form-control mb-2 bg-dark text-light"
        placeholder="Apellido"
        name="rApellido"
        value={rApellido}
        onChange={handleUsuarioInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Email</label>
      <input
        type="email"
        className="form-control mb-2 bg-dark text-light"
        placeholder="Correo"
        name="rEmail"
        value={rEmail}
        autoComplete="new-password"
        onChange={handleUsuarioInputChange}
      />
    </div>
    <div className="form-group text-center mb-4">
    <label style={{color:"white"}}>Contrasea</label>
      <div className='d-flex flex-row align-items-center'>
      <input
        type={isOculto?"password":"text"}
        className="mb-2 bg-dark form-control text-light"
        placeholder="Contrasea"
        name="rPassword"
        value={rPassword}
        autoComplete="new-password"
        onChange={handleUsuarioInputChange}
      />
      <button style={{width:"15%",height:"15%"}} className={isOculto?'btn btn-success btn-sm mb-2 ms-1':'btn btn-danger btn-sm mb-2 ms-1'} onClick={setOculto} type="button">{isOculto?<i className="fa-solid fa-eye"></i>:<i className="fa-solid fa-eye-slash"></i>}</button>
      </div>
    </div>

    <div className="form-group d-flex justify-content-center mb-1">
      <input type="submit" className="btn btn-success" value="Guardar" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btn btn-light mt-2" onClick={handleVolver}>Volver</button>
  </div>
      {/* <div className="m-1 w-50 d-flex flex-column justify-content-center align-items-center">
        <span className="text-light">Email</span>
        <input className="form-control bg-dark text-light" type="text" value={usuario.email}/>
        <span className="text-light">Nombre</span>
        <input className="form-control bg-dark text-light" type="text" value={usuario.nombre}/>
        <span className="text-light">Apellido</span>
        <input className="form-control bg-dark text-light" type="text" value={usuario.apellido}/>
        <span className="text-light">Apellido</span>
        <input className="form-control bg-dark text-light" type="text"/>
        <span className="text-light">Apellido</span>
        <input className="form-control bg-dark text-light" type="text"/>
      </div> */}
      
    </div>
  );
};
