import React, { useEffect } from "react";
import { useState } from "react";
import { fetchSinToken } from "../../helpers/fetch";
import {isMobile} from 'react-device-detect';
import { useSelector } from "react-redux";
import { LikeSect } from "./LikeSect";
import { Link } from "react-router-dom";

export const FotosScreen = () =>  {
  const [fotos, setFotos] = useState([]);
  const [eventos, setEventos] = useState([]);
  const { uid } = useSelector((state) => state.auth);
  useEffect(() => {
    const getFotos = async() =>{
      const resp = await fetchSinToken("eventos/fotos");
      const body = await resp.json();
      setFotos(body.fotos);
    }
    const getEventos = async() =>{
      const resp = await fetchSinToken("eventos");
      const body = await resp.json();
      setEventos(body.eventos);
    }
    getFotos();
    getEventos();
  }, []);
  return (
    <>
    { isMobile ?
    <div className="container-fluid d-flex flex-column justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
    <div className="d-flex flex-column justify-content-center align-items-center">
        <h1 className="text-light">Fotos</h1>
        <h6 className="text-light text-center"><Link to="/login" className="text-decoration-none text-success"><b>Loguea</b></Link> para likear/compartir las fotos</h6>
      </div>
      {eventos && eventos.map((evento,i) => {
        return (
          <div key={evento.id}>
            <hr className="bg-light"/>
            <h1 className="text-center">{evento.nombre}</h1>
            <h4 className="text-center text-light">{evento.ubicacion}</h4>
            <div className="row">
            {fotos && fotos.map((foto) => {
              return (
              foto.evento===evento.id &&
              <div key={foto.id} className="mb-4" data-aos="zoom-in-up" data-aos-duration="1000">
                <LikeSect foto={foto} evento={evento} likess={foto.likes} likee={foto.usuarios.map((usuario)=>{if(usuario.usuario===uid){return usuario.like}})} />
              </div>
            )})}
            </div>
          </div>
      )})
      }
    </div>
    :
    <div className="row d-flex justify-content-center align-items-center pe-5 ps-5" data-aos="fade-up" data-aos-duration="1000">
      <div className="d-flex flex-column justify-content-center align-items-center">
        <h1 className="text-light">Fotos</h1>
        {!uid&&<h6 className="text-light"><Link to="/login" className="text-decoration-none text-success"><b>Loguea</b></Link> para likear/compartir las fotos</h6>}
      </div>
      {eventos && eventos.map((evento,i) => {
        return (
          <div key={evento.id}>
            <h1 className="text-center">{evento.nombre}</h1>
            <h4 className="text-center text-light">{evento.ubicacion}</h4>
            <div className="row">
            {fotos && fotos.map((foto) => {
              return (
              foto.evento===evento.id &&
              <div key={foto.id} className="col-6 mb-5" data-aos="zoom-in-up" data-aos-duration="1000">
                <LikeSect foto={foto} evento={evento} likess={foto.likes} likee={foto.usuarios.map((usuario)=>{if(usuario.usuario===uid){return usuario.like}})} />
              </div>
            )})}
            </div>
          </div>
      )})
      }
    </div>
    }
  </>
  );
};
