import React, { useEffect } from "react";
import { PrivateRoute } from "./PrivateRoute";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { startChecking } from "../actions/auth";
import { Loading } from "./Loading";
import { LoginScreen } from "../components/auth/LoginScreen";
import { MainScreen } from "../components/ui/MainScreen";
import { RegisterScreen } from "../components/auth/RegisterScreen";
// import { LocationScreen } from "../components/ui/LocationScreen";
import { EventoDetalle } from "../components/eventos/EventoDetalle";
import { ClienteDescargaPDF } from "../components/clientes/ClienteDescargaPDF";
import { EventosScreen } from "../components/ui/EventosScreen";
import { NotFoundScreen } from "../components/ui/NotFoundScreen";
import { Layout } from "./Layout";
import { PerfilScreen } from "../components/ui/PerfilScreen";
import { AddTicket } from "../components/eventos/AddTicket";
import { AddTicketFree } from "../components/eventos/AddTicketFree";
import { Pagos } from "../components/ui/Pagos";
import { CredencialesScreen } from "../components/ui/CredencialesScreen";
import { EntradasScreen } from "../components/ui/EntradasScreen";
import { FotosScreen } from "../components/ui/FotosScreen";
import { PublicRoute } from "./PublicRoute";
import { Finalizar } from "../components/ui/Finalizar";
import { EventosBuscados } from "../components/ui/EventosBuscados";
import { OkScreen } from "../components/ui/OkScreen";
import { HistorialCompraScreen } from "../components/ui/HistorialCompraScreen";

export const AppRouter = () => {
  const dispatch = useDispatch();
  const { checking, uid } = useSelector((state) => state.auth);
  // const { rol } = useSelector((state) => state.auth);
  // const [isAdmin, setisAdmin] = useState(false);
  // const [isCliente, setisCliente] = useState(false);
  // useEffect(() => {
  //   if(rol === 'ADMIN'){
  //     setisAdmin(true);
  //   }
  //   if(rol === 'CLIENTE'){
  //     setisCliente(true);
  //   }
  // }, [setisAdmin,setisCliente,rol]);

  useEffect(() => {
    dispatch(startChecking());
  }, [dispatch]);
  if (checking) {
    return <Loading checking={checking} />;
  }

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route element={<PrivateRoute isAuthenticated={!!uid} />}>
            <Route path="misdatos" element={<PerfilScreen />} />
            <Route path="misentradas" element={<EntradasScreen />} />
            <Route path="compras" element={<HistorialCompraScreen />} />
            <Route path="miscredenciales" element={<CredencialesScreen />} />
            <Route path="addticket" element={<AddTicket />} />
            <Route path="addticketfree" element={<AddTicketFree />} />
            <Route path="pagos" element={<Pagos />} />
            <Route path="finalizar" element={<Finalizar />} />
          </Route>
          <Route index element={<MainScreen />} />
          <Route element={<PublicRoute isAuthenticated={!!uid} />}>
            <Route path="login" element={<LoginScreen />} />
            <Route path="register" element={<RegisterScreen />} />
          </Route>
          <Route path="descargapdf/:idevento/:id" element={<ClienteDescargaPDF />} />
          <Route path="evento/:idevento" element={<EventoDetalle />} />
          {/* <Route path="location" element={<LocationScreen/>} /> */}
          <Route path="eventos" element={<EventosScreen />} />
          <Route path="busqueda" element={<EventosBuscados />} />
          <Route path="fotos" element={<FotosScreen />} />
          <Route path="ok" element={<OkScreen />} />
          <Route path="*" element={<NotFoundScreen />} />
        </Route>
      </Routes>
    </Router>
  );
};
